	const onlyInParent= false;
  const content= '';
	workspace.addEditor(inputEvent, onlyInParent, content, fileName, path);
	showModalNewFile.value = false;
};

const openFileModal = (
	inputEvent: Event,
	workspace: Workspace,
	fileName: string,
	path: string,
	content='') => {
	const onlyInParent= false;
